@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@300;500;700&family=Roboto:wght@100;300;400;500;700;900&display=swap");

.aboutpage {
  background-color: var(--pastel-green);
  overflow: hidden;
  margin-left: 15vh; 
}

.aboutpage .row {
  min-height: 100vh;
}

.col-photo {
  background-image: url("../Images/profile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  min-height: 35rem;
}

.col-about-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.aboutpage h1 {
  font-family: "Oswald", sans-serif;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 4rem;
  margin: 1.5rem 0;
}

.aboutpage h2 {
  font-family: "Oswald", sans-serif;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 1.5rem;
  margin: 1rem 0 1.5rem 0;
}

.aboutpage p {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: var(--grey-dark);
  padding: 0 5rem;
}

.aboutpage .line {
  height: 10px;
  width: 100px;
  border-bottom: 3px solid var(--grey-dark);
  margin-bottom: 1.5rem;
}

.aboutpage .line:hover {
  width: 250px;
  transition: 1s ease-in-out;
}

.col-about-text span {
  font-weight: 500;
  /* color: #86a58e; */
}

/* TEXT SELECTION COLOR AND BACKGROUND */

.aboutpage h1::selection,
.aboutpage p::selection,
.aboutpage h2::selection {
  color: white;
  background-color: var(--grey-dark);
}

/* ICONS STYLING */

.icons {
  display: flex;
  flex-direction: row;
}

.icons .icon-paragraph {
  padding: 0 1rem;
  font-size: 1.3rem;
  position: relative;
  display: inline-block;
  color: #86a58e;
}

.icons .icon-paragraph:hover {
  transform: rotate(10deg);
  transition: transform 0.5s ease-in-out;
}

#iconTip,
#swimTip,
#sportTip,
#foodTip {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

/* RESPONSIVE SIZES */

@media only screen and (max-width: 992px) {
  .aboutpage {
    overflow: initial;
    margin-left: 0;
    padding-bottom: 2rem;
    overflow-x: hidden;
  }

  .aboutpage p {
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    color: var(--grey-dark);
    padding: 0 2rem;
  }

  .aboutpage h1 {
    padding: 0 2rem; 
  }

  .col-photo {
    width: 100%;
    margin: 0;
    padding: 0;
  }
} 
