.portfoliopage {
  background-color: var(--pastel-yellow);
  overflow: hidden;
  min-height: 100vh;
  margin-left: 15vh;
}

.portfoliopage .row {
  height: 100vh;
}

.portfolio-home {
  height: 100vh;
  background-image: url("../Images/portfolio-main.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.col-portfolio-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.portfoliopage h1 {
  font-family: "Oswald", sans-serif;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 4rem;
  margin: 1.5rem 0;
}

.portfoliopage h2 {
  font-family: "Oswald", sans-serif;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 1.5rem;
  margin: 1rem 0 1.5rem 0;
}

.portfoliopage p {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: var(--grey-dark);
  padding: 0 7rem;
  margin-top: 2rem;
  margin-bottom: 3rem;
}

.portfoliopage .line {
  height: 10px;
  width: 100px;
  border-bottom: 3px solid var(--grey-dark);
  margin-bottom: 1.5rem;
}

.portfoliopage .line:hover {
  width: 250px;
  transition: 1s ease-in-out;
}

/* LIST ITEMS */

.portfoliopage ul {
  list-style: none;
  text-align: left;
}

.portfoliopage ul li {
  margin-bottom: 1rem;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
}

.portfoliopage ul li:hover {
  transform: scale(1.1);
}

.portfoliopage ul li span {
  margin-left: 0.5rem;
  text-transform: lowercase;
  color: #aea887;
  font-weight: 300;
}

/* TEXT SELECTION COLOR AND BACKGROUND */

.portfoliopage h1::selection,
.portfoliopage ul li span::selection,
.portfoliopage ul li::selection {
  color: white;
  background-color: var(--grey-dark);
}

/* RESPONSIVE SIZES */

@media only screen and (max-width: 992px) {
  .portfoliopage {
    overflow: initial;
    margin-left: 0;
    padding-bottom: 2rem;
    overflow-x: hidden;
  }

  .portfoliopage h1 {
    padding: 0 2rem;
    margin: 7rem 0 1rem 0;
  }

  .portfoliopage ul li span {
    display: block;
    margin-left: 0;
  }

  .portfoliopage ul {
    margin-left: 0;
    padding-left: 0; 
  }

  .portfoliopage ul li {
    margin-bottom: 0.25rem;
  }
}
