.skillspage {
  background-color: var(--pastel-orange);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  min-height: 100vh;
  margin-left: 15vh;
}

.skillspage h1 {
  font-family: "Oswald", sans-serif;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 4rem;
  margin: 1.5rem 0;
}

.skillspage p {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: var(--grey-dark);
  padding: 0 7rem;
  margin-top: 2rem;
  margin-bottom: 3rem;
}

.skillspage span {
  font-weight: 500;
}

.skillspage .line {
  height: 10px;
  width: 100px;
  border-bottom: 3px solid var(--grey-dark);
  margin: 0 auto;
}

.skillspage .line:hover {
  width: 250px;
  transition: 1s ease-in-out;
}

.skillspage ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.designer {
  font-family: "Oswald", sans-serif;
  display: inline-block;
  background-color: #c3a484;
  padding: 10px 15px;
  font-size: 2rem;
  font-weight: 900;
  text-transform: uppercase;
}

.webdesigner {
  font-family: "Oswald", sans-serif;
  display: inline-block;
  background-color: var(--grey-dark);
  color: white;
  padding: 10px 15px;
  font-size: 2rem;
  font-weight: 900;
  text-transform: uppercase;
}

.designer:hover{
  transform: scale(1.05);
  transition: transform 0.2s ease-in;
}

.webdesigner:hover{
  transform: scale(1.05);
  transition: transform 0.2s ease-in;
}

.skillspage h3 {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  font-size: 1.25rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-weight: 900;
}

.col-md-4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* TEXT SELECTION COLOR AND BACKGROUND */

.skillspage h1::selection,
.skillspage p::selection,
.skillspage h3::selection,
.skillspage li::selection,
.webdesigner::selection,
.designer::selection {
  color: white;
  background-color: var(--grey-dark);
}

/* RESPONSIVE SIZES */

@media only screen and (max-width: 992px) {
  .skillspage {
    overflow: initial;
    margin-left: 0;
    padding-bottom: 2rem;
    overflow-x: hidden;
  }

  .skillspage p {
    padding: 0 2rem;
  }

  .skillspage h1 {
    padding: 0 2rem;
    margin: 7rem 0 1rem 0;
  }

  .pieChart {
    width: 200px;
    margin: 1rem 0;
    padding-bottom: 2.5rem;
  }
}
