/* NAVBAR STYLING */

/* NAVBAR WEBSITE (VERTICAL) */

.nav-web {
  transform-origin: left top;
  transform: rotate(-90deg) translateX(-100%);
  width: 100vh;
  height: 15vh;
  background-color: white;
  position: absolute;
  z-index: 10;
}

.nav-web ul {
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  list-style: none;
}

.nav-web ul li {
  padding: 0.5rem 2rem;
  text-transform: uppercase;
  font-size: 1rem;
  font-family: "Oswald", sans-serif;
}

.nav-web ul li a:hover {
  color: #262626;
  color: var(--grey-dark);
  text-decoration: none;
}

.nav-web ul li a {
  color: #c0c0c0;
  color: var(--grey-light);
}

.nav-web .logo {
  font-family: "Roboto", sans-serif;
  font-weight: 800;
  font-size: 2.5rem;
  display: flex;
  flex: 0 1;
  flex-basis: 50px;
  content: " ";
  transform: rotate(90deg);
}

.active {
  color: #262626;
  color: var(--grey-dark);
}

/* NAVBAR MOBILE (HORIZONTAL) */

.nav-mobile{
  z-index: 100;
  display: none;
}

.navbar{
  display: none !important;
}

.bg-light {
  background-color: white !important;
}

/* RESPONSIVE SIZES */

@media screen and (max-width: 992px) {
  .nav-web {
    display: none;
    margin-right: 0;
  }

  .navbar{
    display: block !important;
    margin-right: 0;
  }
}

.homepage {
  overflow: hidden;
  margin-left: 15vh;
  height: 100vh;
  background-color: var(--pastel-blue);
  /* background-image: url("../Images/background-homepage.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.homepage h1 {
  font-family: "Oswald", sans-serif;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 7rem;
  margin: 1.5rem 0 0 0;
}

.homepage h2 {
  font-family: "Oswald", sans-serif;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 1.5rem;
  margin: 1rem 0 1.5rem 0;
}

.homepage .graphic {
  font-family: "Courgette", cursive;
  text-transform: lowercase;
  font-size: 2rem;
}

.homepage .developer {
  font-family: "Courgette", cursive;
  text-transform: uppercase;
  -webkit-text-decoration: wavy;
          text-decoration: wavy;
  font-size: 1.75rem;
  font-family: "Source Code Pro", monospace;
  letter-spacing: -3px;
}

/* BUTTON */

.homepage button {
  padding: 0.5rem 0.75rem;
  background-color: white;
  border: none;
  color: #84a0b6;
  width: 10rem;
  margin-top: 1.5rem;
}

.homepage button:hover {
  border: none;
  background-color: #84a0b6;
  color: white;
  transition: background-color 0.25s ease-in;
}

/* RESPONSIVE SIZES */

@media only screen and (max-width: 992px) {
  .homepage {
    margin-left: 0;
    padding-bottom: 2rem;
    min-height: 100vh;
    overflow: hidden;
  }

  .homepage h1 {
    padding: 0 2rem;
    font-size: 6rem;
    line-height: 1;
  }

  .homepage .graphic {
    display: block;
    margin-bottom: 0.5rem;
  }

  .homepage .developer {
    display: block;
    margin-top: 0.3rem;
  }
}

