.contactpage {
  background-color: var(--pastel-red);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  min-height: 100vh;
  margin-left: 15vh;
}

.contactpage h1 {
  font-family: "Oswald", sans-serif;
  color: var(--grey-dark);
  text-transform: uppercase;
  font-size: 4rem;
  margin: 1.5rem 0;
}

.contactpage .line {
  height: 10px;
  width: 100px;
  border-bottom: 3px solid var(--grey-dark);
  margin-bottom: 1.5rem;
}

.contactpage .line:hover {
  width: 250px;
  transition: 1s ease-in-out;
}

.contactpage form {
  width: 40vw;
  padding-top: 1rem;
  display: flex;
  flex-direction: column;
}

.contactpage form input {
  margin-bottom: 1rem;
  border: none;
  padding: 0.35rem 0.75rem;
}

.contactpage form input:focus,
.contactpage form input:active,
.contactpage form textarea:focus,
.contactpage form textarea:active {
  outline: 2px solid #ac7574;
}

.contactpage form textarea {
  margin-bottom: 1rem;
  border: none;
  padding: 0.35rem 0.75rem;
}

/* SUBMIT BUTTON */

.btn-container {
  display: flex;
  flex-direction: column;
}

.contactpage button {
  padding: 0.35rem 0.75rem;
  background-color: white;
  border: none;
  color: #ac7574;
  width: 10rem;
  align-self: flex-end;
}

.contactpage button:hover {
  border: none;
  background-color: #ac7574;
  color: white;
  transition: background-color 0.25s ease-in;
}

/* TEXT SELECTION COLOR AND BACKGROUND */

.contactpage h1::selection,
.contactpage p::selection,
.contactpage form input::selection,
.contactpage button::selection,
.contactpage form textarea::selection {
  color: white;
  background-color: var(--grey-dark); 
}

/* RESPONSIVE SIZES */

@media only screen and (max-width: 992px) {
  .contactpage {
    overflow: visible;
    overflow: initial;
    margin-left: 0;
    padding-bottom: 2rem;
    overflow-x: hidden;
  }

  .contactpage p {
    padding: 0 2rem; 
  }

  .contactpage h1 {
    padding: 0 2rem;
    margin: 7rem 0 1rem 0;
  }

  .contactpage form {
    width: 85vw;
  }
}

